/**
 * 
 */
export default {
  EPSG3857: {
    /**
     * 
     */
    GoogleMap: { //http://www.google.cn/maps/vt?lyrs={v}@{s}&gl=cn&x={x}&y={y}&z={z}
      url: 'http://mt{s}.google.cn/vt/lyrs={v}&hl=zh-CN&gl=cn&x={x}&y={y}&z={z}',
      options: {
        subdomains: '0123'
      },
      v: {
        Normal: 'm', //
        Satellite_N: 's', //
        Satellite: 'y', //()
        Terrain: 't', //
        Terrain_R: 'p', //+
        Skeleton_D: 'r', //dark
        Skeleton_H: 'h', //light
        Hybrid: 'y' ///?
      }
    },
    /**
     * 
     */
    BaiduMap: {
      url: "http://online{s}.map.bdimg.com/tile/?qt=tile&x={x}&y={y}&z={z}&styles={v}&v=020",
      options: {
        minZoom: 3,
        maxZoom: 19,
        subdomains: '0123456789',
        tms: true
      },
      v: {
        Normal: 'pl',
        A: 'sl',
        Satellite: {
          url: 'http://shangetu{s}.map.bdimg.com/it/u=x={x};y={y};z={z};v=009;type=sate&fm=46'
        },
      }
    },
    /**
     *  
     */
    Tianditu: {
      url: 'http://t{s}.tianditu.com/DataServer?T={v}&x={x}&y={y}&l={z}',
      // url = 'http://t{s}.tianditu.com/cva_c/wmts/{z}/{y}/{x}' 
      // url = 'http://t{s}.tianditu.cn/vec_c/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=c&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&FORMAT=tiles',//4326
      options: {
        subdomains: '01234567',
      },
      v: {
        Normal: 'vec_w', //
        Normal_A: 'cva_w', //-
        Terrain: 'ter_w', //
        Terrain_A: 'cta_w', //-
        Satellite: 'img_w', //
        Satellite_A: 'cia_w', //-
      }
    },
    /**
     * 
     */
    GaodeMap: {
      url: 'http://web{v}0{s}.is.autonavi.com/appmaptile?lang=zh_cn&style={style}&x={x}&y={y}&z={z}',
      options: {
        minZoom: 3,
        subdomains: '1234',
        attribution: '&copy; <a>GaoDeMap</a>'
      },
      v: {
        Normal: { // 
          options: {
            v: 'rd',
            style: 8
          }
        },
        Vector: { // 
          options: {
            v: 'rd',
            style: 7
          }
        },
        Satellite: { // 
          options: {
            v: 'st',
            style: 6
          }
        },
        Satellite_A: { // -
          options: {
            v: 'st',
            style: 8
          }
        }
      }
    },
    /**
     * 
     */
    TencentMap: {
      url: 'http://rt{s}.map.gtimg.com/realtimerender?z={z}&x={x}&y={y}&type=vector&style={v}',
      options: {
        subdomains: '012'
      },
      v: {
        Normal: '0',
        Normal_R: '1',
        Terrain: 'demTiles', //
        Satellite: 'sateTiles' //
      }
    }
  },
  EPSG4326: {
    /**
     *  4490
     */
    Tianditu_DataServer: {
      url: 'http://t{s}.tianditu.com/DataServer?T={v}&x={x}&y={y}&l={z}',
      options: {
        zoomOffset: 1,
        subdomains: '01234567'
      },
      v: {
        Normal: 'vec_c', //
        Normal_A: 'cva_c', //-
        Terrain: 'ter_c', //
        Terrain_A: 'cta_c', //-
        Satellite: 'img_c', //-
        Satellite_A: 'cia_c', //-
      }
    },
    Tianditu: {
      url: 'http://t{s}.tianditu.com/{v}_c/wmts?SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER={v}&STYLE=default&TILEMATRIXSET=c&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&FORMAT=tiles',
      options: {
        zoomOffset: 1,
        subdomains: '01234567'
      },
      v: {
        Normal: 'vec', //
        Normal_A: 'cva', //-
        Terrain: 'ter', //
        Terrain_A: 'cta', //-
        Satellite: 'img', //-
        Satellite_A: 'cia', //-
      }
    }

  }
}



